import * as React from "react";
import { useEffect, useState } from "react";
import { MdOutlineFileDownload } from "react-icons/md";
import SettingHeader from "../../../components/SettingHeader";
import Table from "../../../components/Table";
import SearchBar from "../../../components/SearchBar";
import ConfirmationModal from "../../../components/ConfirmationModal";
import { Card, Button, Tooltip, Select, Option } from "@material-tailwind/react";
import { T_Payloads, T_ConfirmationModalState } from "../../../utils/setting.type";
import service from "../../../../../utils/services";
import { settingDataList } from '../../../../../utils/options';
import { toast } from 'react-toastify';

type T_Template = {
  id: string;
  theme: string;
  is_active: boolean;
  company_id: string;
  name: string;
};

type T_TemplateRender = {
  id: string;
  theme: string;
  is_active: JSX.Element;
  company_id: string;
  name: string;
};

const SettingMailEngine: React.FC = () => {
  const [payloadList, setPayloadList] = useState<T_Payloads>({search: "", page: 1, size: 10, sort: "", company_id: ""});
  const [templateList, setTemplateList] = useState<T_Template[]>([]);
  const [templateListRender, setTemplateListRender] = useState<T_TemplateRender[]>([]);
  const [totalData, setTotalData] = useState<number>(0);
  const [selectedData, setSelectedData] = useState<T_Template>({id: '', theme: '', is_active: false, company_id: '', name: ''});
  const [loading, setLoading] = useState<boolean>(false);

  const [confirmationModalBox, setConfirmationModalBox] = useState<T_ConfirmationModalState>({visible: false, mode: "Edit", loading: false});

  const titles: string[] = ["Theme", "Company Name", "Status"];
  const keyLists: string[] = ["theme", "name", "is_active"];

  useEffect(() => {
    getTemplateDataList();
  }, [payloadList]);

  useEffect(() => {
    setTemplateListRender(
      templateList.map((item) => {
        return {
          ...item,
          is_active: (
            <div className="flex gap-1 items-center justify-center">
              <Tooltip
                content="Change status"
                placement="top"
                className="text-xs py-2 px-4"
              >
                <Select
                  variant="outlined"
                  color={item.is_active ? "blue" : "red"}
                  value={item.is_active ? "Active" : "Inactive"}
                  className={("!border !border-transparent font-bold ") + (item.is_active ? "bg-blue-50 text-blue-700" : "bg-red-50 text-red-700")}
                  labelProps={{className: 'hidden' }}
                  containerProps={{className: '!w-[100px] !min-w-0 !h-[30px]'}}
                  onChange={(e) => {
                    const value = e === 'Active' ? true : false;
                    if(item.is_active != value) {
                      setSelectedData({...item, is_active: value});
                      setConfirmationModalBox({visible: true, mode: "Edit", loading: false});
                    }
                  }}
                >
                  <Option value="Active">Active</Option>
                  <Option value="Inactive">Inactive</Option>
                </Select>
              </Tooltip>
            </div>
          ),
        };
      })
    );
  }, [templateList]);

  useEffect(() => {
    console.log(selectedData);
  }, [selectedData]);

  const getTemplateDataList = async () => {
    try {
      setLoading(true);
      const response = await settingDataList.mail_engine(payloadList);
      console.log(response.data);
      
      setTemplateList(response.data);
      setTotalData(response.totalData);
      setLoading(false);
    } catch (error) {
      console.log(error.response.message);
      toast.error(error.response.message);
    }
  };

  const onChangeStatus = async () => {
    try {
      setLoading(true);
      setConfirmationModalBox((prev) => ({...prev, loading: true}));
      const response = await service.post('/setting/mail_engine/change_status', selectedData);

      if(response.statusCode === 200) {
        toast.success(response.message);
        getTemplateDataList();
        setConfirmationModalBox((prev) => ({...prev, visible: false, loading: false}));
      }
    } catch (error) {
      console.log(error.response.message);
      toast.error(error.response.message);
    }
  };

  return (
    <div className="flex flex-col gap-y-4">
      <SettingHeader title="Mail Engine" buttonAction={() => {}}/>

      <Card className="flex flex-col px-4 py-8 gap-5 shadow-none">
        <div className="flex flex-row flex-wrap sm:flex-nowrap gap-2">
          <SearchBar setPayload={setPayloadList} loadingState={loading}/>

          <Button
            variant="outlined"
            color="gray"
            className="flex w-full sm:max-w-fit gap-x-1 py-2 px-4 normal-case border-gray-300 items-center justify-center hover:bg-gray-100"
          >
            <MdOutlineFileDownload size={20} />
            Download List Mail Engine
          </Button>
        </div>

        <Table
          indexList={titles}
          indexKey={keyLists}
          indexData={templateListRender}
          maxData={totalData}
          payload={payloadList}
          setPayload={setPayloadList}
          loadingState={loading}
        />

        <ConfirmationModal
          title={`Are you sure want to change status to ${selectedData.is_active ? "Active" : "Inactive"}?`}
          subTitle={`If you set the chosen data to ${selectedData.is_active ? "Active" : "Inactive"}, it ${selectedData.is_active ? "will" : "will not"} be shown on the "Mail Template" list.`}
          modalState={confirmationModalBox}
          setModalState={setConfirmationModalBox}
          action={onChangeStatus}
          onCancel={getTemplateDataList}
        />
      </Card>
    </div>
  );
};

export default SettingMailEngine;

